const timeUtil = {
  // ÌòÑÏû¨ ÏãúÍ∞ÑÍ∞íÏùÑ Í≥ÑÏÇ∞Ìï¥ÏÑú Ï§ëÎ≥µÏù¥ ÎêòÏßÄ ÏïäÎäî Í∞íÏùÑ Ï≤òÎ¶¨ÌïúÎã§.
  // Ïö©ÎèÑÎäî key ÏôÄ id Î•º ÏÉùÏÑ±Ìï¥ Ï£ºÍ∏∞ ÏúÑÌï¥ÏÑú Ï≤òÎ¶¨
  // 10Î≥¥Îã§ ÏûëÏùÄ Í∞íÏóê 0ÏùÑ Î∂ôÏûÑ
  addZero(n) {
    return n < 10 ? "0" + n : n;
  },
  // ÌòÑÏû¨ ÏãúÍ∞ÑÏùÑ Î¶¨ÌÑ¥
  getCurrentDate() {
    let date = new Date();
    return (
      date.getFullYear().toString() +
      this.addZero(date.getMonth() + 1) +
      this.addZero(date.getDate()) +
      this.addZero(date.getHours()) +
      this.addZero(date.getMinutes()) +
      this.addZero(date.getSeconds())
    );
  },
  getCurrentTime() {
    let date = new Date();
    return (
      date.getFullYear().toString() +
      "ÎÖÑ" +
      "\u00a0\u00a0" +
      this.addZero(date.getMonth() + 1) +
      "Ïõî" +
      "\u00a0" +
      this.addZero(date.getDate()) +
      "Ïùº" +
      "\u00a0\u00a0" +
      this.addZero(date.getHours()) +
      ":" +
      this.addZero(date.getMinutes())
    );
  },
};

const state = {
  headerText: "My ToDo List",
  memoItemArr: [],
  iconArr: ["Î¨∏ÏÑú1.png", "Î¨∏ÏÑú2.png"],
};

const actions = {
  fetchReadMemo(context) {
    axios
      .get("http://di1025.dothome.co.kr/page-miniblog/read.php")
      .then((response) => {
        console.log(response.data);
        context.commit("READ_MEMO", response.data.result);
      })
      .catch((err) => console.log(err));
  },

  fetchAddMemo(context, obj) {
    // ÏÑúÎ≤ÑÏóê Ï£ºÏÜåÎ°ú Ï†ëÍ∑ºÌïòÏó¨ÏÑú ÏûêÎ£åÎ•º push ÌïúÎã§.
    // push ÌïòÍ≥† ÎÇòÏÑú Ï†ïÏÉÅÏ†ÅÏúºÎ°ú Ï∂îÍ∞ÄÎêòÏóàÎã§Î©¥
    // ÏïÑÎûòÏùò Î™ÖÎ†πÏùÑ Ïã§ÌñâÌïúÎã§.

    let addData = {
      user: timeUtil.getCurrentDate(),
      title: obj.item,
      date: timeUtil.getCurrentTime(),
      icon: state.iconArr[obj.index],
    };

    axios
      .get(
        `http://di1025.dothome.co.kr/page-miniblog/write.php?user=${addData.user}&title=${addData.title}&date=${addData.date}&icon=${addData.icon}`
      )
      .then((response) => {
        // console.log(response.data);
        if (response.data.result == 1) {
          // console.log('Î™©Î°ùÍ∞ÄÏ†∏Ïò§Í∏∞');
          context.commit("ADD_MEMO", addData);
        } else {
          console.log("ÏÑúÎ≤ÑÏóêÎü¨");
        }
      })
      .catch((err) => console.log(err));
  },
  fetchDeleteMemo({ commit }, obj) {
    // ÏÑúÎ≤ÑÏóê Ï£ºÏÜåÎ°ú Ï†ëÍ∑ºÌï¥ÏÑú Îç∞Ïù¥ÌÑ∞Î•º DELETE.
    // DELETE Í∞Ä ÏÑ±Í≥µÌñàÎã§Î©¥
    // ÏïÑÎûòÎ•º Ïã§ÌñâÌïúÎã§.
    axios
      .get(`http://di1025.dothome.co.kr/page-miniblog/delete.php?id=${obj.id}`)
      .then((response) => {
        console.log(response.data);
        commit("DELETE_MEMO", obj);
      })
      .catch((err) => console.log(err));
  },
  fetchUpdateMemo({ commit }, obj) {
    // ÏÑúÎ≤ÑÏùò Ï£ºÏÜåÎ°ú Ï†ëÍ∑ºÌï¥ÏÑú FETCH ÌïúÎã§.
    // Ï†ïÏÉÅÏ†ÅÏúºÎ°ú Ï≤òÎ¶¨ÎêòÏóàÎã§Î©¥
    // ÏïÑÎûòÎ•º Ïã§ÌñâÌïúÎã§.

    let complete = 1;
    if (obj.item.complete == true) {
      complete = 0;
    } else {
      complete = 1;
    }

    axios
      .get(
        `http://di1025.dothome.co.kr/page-miniblog/update.php?id=${obj.item.id}&complete=${complete}`
      )
      .then((response) => {
        console.log("ÏóÖÎç∞Ïù¥Ìä∏", response.data);
        commit("UPDATE_MEMO", obj);
      })
      .catch((err) => console.log(err));
  },
  fetchClearMemo({ commit }) {
    // ÏÑúÎ≤ÑÏùò Ï£ºÏÜåÎ°ú Ï†ëÍ∑ºÌï¥ÏÑú DELETE ÌïúÎã§.
    // Ï†ïÏÉÅÏ†ÅÏúºÎ°ú Ï≤òÎ¶¨ÎêòÏóàÎã§Î©¥
    // ÏïÑÎûòÎ•º Ïã§ÌñâÌïúÎã§.

    axios
      .get("http://di1025.dothome.co.kr/page-miniblog/delete.php?id=all")
      .then((response) => {
        console.log("Ï†ÑÏ≤¥ÏÇ≠Ï†ú", response.data);
        commit("CLEAR_MEMO");
      })
      .catch((err) => console.log(err));
  },
};
const mutations = {
  READ_MEMO(state, payload) {
    payload.forEach((item) => {
      if (item.complete === "0") {
        item.complete = false;
      } else {
        item.complete = true;
      }
    });

    state.memoItemArr = payload;
  },

  // ÏïÑÏù¥ÌÖú Ï∂îÍ∞Ä {item, index}
  ADD_MEMO(state, payload) {
    // json Ï†ÄÏû• Î¨∏ÏûêÏó¥
    ///{completed:false, title:Î©îÎ™®ÎÇ¥Ïö©, icon:ÌååÏùºÎ™Ö ....}
    // ÏïÑÏù¥ÏΩò Í¥ÄÎ†® Ï≤òÎ¶¨
    // let memoTemp = {
    //     id: timeUtil.getCurrentDate(),
    //     complete: false,
    //     memotitle: payload.item,
    //     memodate: timeUtil.getCurrentTime(),
    //     memoicon: state.iconArr[payload.index]
    // };
    // // Ï∂îÌõÑ Ïã§Ï†ú DB Ïó∞Îèô ÏòàÏ†ï
    // localStorage.setItem(memoTemp.id, JSON.stringify(memoTemp));
    // ÌôîÎ©¥Í∞±Ïã†ÏùÑ ÏúÑÌïú Î∞∞Ïó¥ ÏöîÏÜå Ï∂îÍ∞Ä
    // state.memoItemArr.push(memoTemp);

    // axiosÎ•º Ïù¥Ïö©Ìï¥ÏÑú Ï∂îÍ∞ÄÎêú Îç∞Ïù¥ÌÑ∞Ïùò Ï†ïÎ≥¥Î•º
    // Í∞ÄÏßÄÍ≥† ÏôÄÏÑú Î™©Î°ù 1Í∞úÎ•º Ï∂îÍ∞ÄÌïúÎã§.
    axios
      .get(
        `http://di1025.dothome.co.kr/page-miniblog/get.php?user=${payload.user}`
      )
      .then((response) => {
        // console.log('ÏûÖÎ†•ÏôÑÎ£å', response);
        const obj = response.data.result[0];
        obj.complete = false;
        state.memoItemArr.push(obj);
      })
      .catch((err) => console.log(err));
  },
  // ÏïÑÏù¥ÌÖú ÏÇ≠Ï†ú {item, index}
  DELETE_MEMO(state, payload) {
    // localStrage ÏóêÏÑú keyÎ•º ÌÜµÌï¥ÏÑú ÏßÄÏö¥Îã§.
    // localStorage.removeItem(payload.item);
    // Î∞∞Ïó¥(memoItemArr) ÏóêÏÑúÎèÑ ÏßÄÏö¥Îã§.
    state.memoItemArr.splice(payload.index, 1);

    // ÌÇ§Í∞íÏùÑ Ïù¥Ïö©Ìï¥ÏÑú Ï†ïÎ†¨ÌïòÍ∏∞(Ïò§Î¶ÑÏ∞®Ïàú)
    // state.memoItemArr.sort((a, b) => {
    //     if (a.id > b.id) return 1;
    //     if (a.id === b.id) return 0;
    //     if (a.id < b.id) return -1;
    // });
  },
  // ÏïÑÏù¥ÌÖú Î≥ÄÍ≤Ω {item, index}
  UPDATE_MEMO(state, payload) {
    // localStorage ÏóêÏÑúÎäî update Î©îÏÜåÎìúÎ•º ÏßÄÏõêÌïòÏßÄ ÏïäÏäµÎãàÎã§.
    // Ï∞æÏïÑÏÑú ÏßÄÏö∞Í≥†,
    // localStorage.removeItem(payload.item.id);
    // Î≥ÄÍ≤ΩÌïúÎã§.
    // item.complete = !item.complete;
    state.memoItemArr[payload.index].complete =
      !state.memoItemArr[payload.index].complete;
    // Îã§Ïãú set ÌïúÎã§.
    // localStorage.setItem(payload.item.id, JSON.stringify(payload.item));

    // ÌÇ§Í∞íÏùÑ Ïù¥Ïö©Ìï¥ÏÑú Ï†ïÎ†¨ÌïòÍ∏∞(Ïò§Î¶ÑÏ∞®Ïàú)
    // state.memoItemArr.sort((a, b) => {
    //     if (a.id > b.id) return 1;
    //     if (a.id === b.id) return 0;
    //     if (a.id < b.id) return -1;
    // });
  },
  // Ï†ÑÏ≤¥ ÏïÑÏù¥ÌÖú ÏÇ≠Ï†ú
  CLEAR_MEMO(state) {
    // localStorage ÏóêÏÑú ÎÇ¥Ïö© Ï†ÑÏ≤¥ ÏÇ≠Ï†ú
    // Ï∂îÌõÑ DB Ïó∞Îèô ÏòàÏ†ï
    localStorage.clear();
    state.memoItemArr.splice(0);
  },
};
const getters = {
  getMemoArr(state) {
    // Ï°∞Í±¥Ïóê Îî∞ÎùºÏÑú Îã§Î•∏ Í≤∞Í≥ºÎ¨ºÏùÑ ÎèåÎ†§Ï§ÄÎã§.
    return state.memoItemArr;
  },
};

export default {
  state,
  actions,
  mutations,
  getters,
};
