  cursor: pointer;
  background: url("@/assets/images/Î¨∏ÏÑú1.png") no-repeat center;
  background-size: cover;
  margin-top: 7px;
  margin-right: 5px;
}

.img2 {
  display: inline-block;
  width: 35px;
